/* eslint-disable no-console */
import Content from './components/Content.vue'

export default {
  name: 'app',
  components: {
    Content,
  },

  data() {
    return {
      charDelay: 4,
      isSongMuted: true,
      isPowering: false,
      isLoading: false,
      isLoaded: false,
      isManagerMode: false,
      isPrinted: false,
    }
  },

  computed: {
    // isManagerMode() {
    //   return this.$route.name === 'manager'
    // }
  },

  methods: {
    songControl() {
      this.isSongMuted = !this.isSongMuted;

      const song = document.getElementById("audio");

      if (this.isSongMuted) song.pause()
      else song.play();
    },

    powerOn() {
      this.isPowering = true;

      setTimeout(() => {
        if (!this.$refs.effects) return;
        this.$refs.effects.style.opacity = ""
        this.$refs.display.style.background = ""
      }, 1500);

      setTimeout(() => {
        this.isPowering = false;
        this.isLoading = true;
      }, 2500);
    },

    toggleMode() {
      if (this.$route.name === 'manager') {
        this.$router.push({ name: 'commandsEmpty' })
        this.isManagerMode = false;
      }
      else {
        this.$router.push({ name: 'manager' })
        this.isManagerMode = true;
      }
    },

    isPrintDone() {
      setTimeout(() => {
        this.isLoading = false;
        this.isLoaded = true;
      }, 2000);
    },
  },

  watch: {
    '$route'(to, from) {

      if (from.name !== 'manager')
        return;

      if (this.$route.name === 'manager')
        this.isManagerMode = true;

      setTimeout(window.onresize, 100);
      // this.powerOn();
    }
  },

  mounted() {
    setTimeout(() => {
      this.powerOn();
    }, 1500);

    console.log(this)

    const path = localStorage.getItem('path');
    if (path) {
      localStorage.removeItem('path');
      this.$router.push({ path });
    }

    if (this.$route.name === 'manager') {
      this.isManagerMode = true;
    }
    // else {
    //   this.isLoaded = true;
    //   this.$refs.effects.style.opacity = "1"
    //   this.$refs.display.style.background = ""
    // }
  }
}