/* eslint-disable no-console */
import Content from '../../components/Content.vue'
import Test from '../../content/test.json'
import SocialSharing from 'vue-social-sharing';

// Vue.use(SocialSharing);

export default {
    components: {
        Content,
        SocialSharing
    },

    data: () => ({
        charDelay: 8,
        section: 'test',
        cmd: 'test',
        // lastCmd: '',
        currentQuestionNumber: 0,
        // answers: [],
        inputValue: '',
        commandList: ['a', 'b'],
        test: Test,
        green: 0,
        red: 0,
        blue: 0,
        yellow: 0,
        result: false,
        typeCode: "",
        type: "",
        role: "",
        description: ""
    }),

    computed: {
        // lastCmdComputed() {
        //     if (this.lastCmd) {
        //         return "> " + this.lastCmd
        //     }
        // },

        currentQuestion() {
            return this.test['questions'][this.currentQuestionNumber]['question'];
        },

        questionCategory() {
            return this.test['questions'][this.currentQuestionNumber]['category'];
        },

        answerA() {
            return this.test['questions'][this.currentQuestionNumber]['answer_A'];
        },

        answerB() {
            return this.test['questions'][this.currentQuestionNumber]['answer_B'];
        }
    },

    methods: {
        testInit: function () {
          this.currentQuestionNumber = 0;
          this.green = 0;
          this.red = 0;
          this.blue = 0;
          this.yellow = 0;
          this.typeCode = "";
          this.type = "";
          this.role = "";
          this.result = false;
        },

        checkCmd: function (cmd) {
            if (cmd)
                cmd = cmd.toLowerCase().trim().replace(/[/-]/g, '');

            //Проверка на основные команды
            if (this.commandList.includes(cmd)) {
                this.lastCmd = cmd !== '' ? cmd : this.lastCmd;
                this.charDelay = 2;


                if (cmd === 'a')
                    this.calcCategory(this.questionCategory);

                if (this.currentQuestionNumber < 19)
                    this.currentQuestionNumber += 1;
                else this.showResult()
            }
            else {
                //Команды для выхода из Теста
                switch (cmd) {
                    case 'q':
                    case 'exit':
                    case 'home':
                        this.$router.push({ name: 'command', params: { command: 'home' } })
                        break;
                    default:
                        break;
                }
            }

            //Очищаем переменную и инпут
            cmd = "";
            return true;
        },

        calcCategory: function (answer) {
            switch (answer) {
                case "green":
                    this.green += 1;
                    break;
                case "red":
                    this.red += 1;
                    break;
                case "blue":
                    this.blue += 1;
                    break;
                case "yellow":
                    this.yellow += 1;
                    break;
                default:
                    break;
            }
        },

        showResult() {
            if (this.green < 3) this.typeCode += 'I'
            else this.typeCode += 'E';

            if (this.red < 3) this.typeCode += 'N'
            else this.typeCode += 'S';

            if (this.blue < 3) this.typeCode += 'F'
            else this.typeCode += 'T';

            if (this.yellow < 3) this.typeCode += 'P'
            else this.typeCode += 'J';

            this.type = this.test['results'][this.typeCode]['type'];
            this.role = this.test['results'][this.typeCode]['role'];
            this.description = this.test['results'][this.typeCode]['description'];
            this.result = true;
            this.section = 'result';
        },

        // focusInput() {
        //     const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
        //     if (!isMobile) {
        //         if (this.$refs.myInput)
        //             this.$refs.myInput.focus();
        //     }
        // },

        // skipTyping() {
        //     this.charDelay = 0;
        //     setTimeout(() => {
        //         this.charDelay = 2;
        //     }, 50);
        // },

        // caretPos() {
        //     const caretPos = (this.$refs.myInput.value.length - this.$refs.myInput.selectionStart) * 10;
        //     this.$refs.caret.style.marginLeft = '-' + caretPos + 'px';
        // },
    },

    watch: {
        '$route'(to, from) {
            this.cmd = to.params.command;
            if (this.cmd) {
                this.checkCmd(this.cmd);
            }

            if (to.name == 'commandsEmpty') {
                this.section = 'home'
                this.lastCmd = ''
            }
        }
    },

    created() {
        this.cmd = this.$route.params.command;
        if (this.cmd)
            this.checkCmd(this.cmd);
    },
}