<template>
  <div>
    <a
      v-for="job in jobs"
      :key="job.id"
      :href="job.application_url"
      target="_blank"
      class="button button-outer job"
      :onclick="ymJob(job.id)"
    >{{job.full_title}}</a>
  </div>
  <!-- :onclick='"ym(56311342," + "\'reachGoal\','" + job.id + '"); return true;"' -->
</template>

<script>
import Jobs from "../content/jobs";
export default {
  data: () => ({
    jobs: Jobs.jobs
  }),

  computed: {
  },

  methods: {
    ymJob(jobID) {
      return "ym(56311342, 'reachGoal', '" + jobID + "'); return true";
    },

    showItem(items, i) {
      const item = items[i];
      return function () {
        item.classList.toggle('job-show');
      }
    }
  },

  mounted() {
    const items = document.getElementsByClassName('job');
    var delay = 1;

    for (var i = 0; i < items.length; i++ ) {
      delay = i * 50;
      setTimeout(this.showItem(items, i), delay);
    }
  }
};
</script>

<style lang="scss">
  .job {
    display: none;

    &-show {
      display: block;
    }
  }
</style>