/* eslint-disable no-console */
import Content from '../../components/Content.vue'
import Jobs from '../../components/Jobs.vue'
import MiniHelp from '../../components/MiniHelp.vue'
import SocialLinks from '../../components/SocialLinks.vue'
import SideProjects from '../../components/SideProjects.vue'

export default {
  components: {
    Content,
    Jobs,
    MiniHelp,
    SocialLinks,
    SideProjects
  },

  data: () => ({
    charDelay: 4,
    isPrinted: false,
    showJobs: false,
    section: 'home',
    cmd: 'home',
    lastCmd: '',
    historyCmd: [],
    historyPos: 0,
    inputValue: '',
    commandList: ['home', 'stack', 'projects', 'business', 'jobs', 'test', 'help', 'ping', 'cowsay'],
    githubLink: 'https://github.com/dodopizza',
    habrLink: 'https://habr.com/ru/company/dodopizzaio/',
    vkLink: 'https://vk.com/dodopizzaio',
    fbLink: 'https://www.facebook.com/dodopizzaio/',
    tgLink: 'https://teleg.run/dodopizzaio',
    ytLink: 'https://www.youtube.com/user/dodomovie/playlists',
    mobile: false
  }),

  computed: {
    lastCmdComputed() {
      if (this.lastCmd) {
        return "> " + this.lastCmd
      }
    },
  },

  methods: {
    checkCmd: function () {
      this.lastCmd = this.cmd !== '' ? this.cmd : this.lastCmd;
      this.cmd = this.cmd.toLowerCase().trim().replace(/[/-]/g, '');

      const aliasActions = {

        'q|quit|exit|back|return|leave':
          () => this.$router.push({ name: 'command', params: { command: 'home' } }),

        'careers|vc|job|jobs|vacancy|vacancies':
          () => this.cmd = 'jobs',

        'techstack|tech|technology|stack|framework':
          () => this.cmd = 'stack',

        'test':
          () => this.$router.push({ name: 'test' }),

        'github': () => {
          this.cmd = ''
          window.open(this.githubLink, '_blank').focus();
        },
        'habr': () => {
          this.cmd = ''
          window.open(this.habrLink, '_blank').focus();
        },
        'vk': () => {
          this.cmd = ''
          window.open(this.vkLink, '_blank').focus();
        },
        'facebook': () => {
          this.cmd = ''
          window.open(this.fbLink, '_blank').focus();
        },
        'telegram': () => {
          this.cmd = ''
          window.open(this.tgLink, '_blank').focus();
        },
        'youtube': () => {
          this.cmd = ''
          window.open(this.ytLink, '_blank').focus();
        },
      };

      const aliasKey = Object.keys(aliasActions).find(key => key.split('|').some(x => x === this.cmd))
      if (aliasKey)
        aliasActions[aliasKey]()
      else if (this.cmd !== '') {
        // this.lastCmd = this.cmd
        this.section = 'notexist'
      }

      //Проверка на основные команды
      if (this.commandList.includes(this.cmd)) {
        // this.lastCmd = this.cmd
        this.section = this.cmd;
        this.isPrinted = false;

        //пушим команды в историю, чтобы стрелками выбирать их
        this.historyCmd.push(this.cmd);
        this.historyPos = this.historyCmd.length;

        if (this.$route.params.command !== this.cmd)
          this.$router.push({ name: 'command', params: { command: this.cmd } })

      }

      //Очищаем переменную и инпут
      this.cmd = "";
      return true;
    },
    
    focusInput() {
      if (this.mobile !== true) {
        this.$refs.myInput.focus();
      }
    },

    skipTyping() {
      this.charDelay = 0;

      setTimeout(() => {
        this.charDelay = 4;
      }, 100);
    },

    prevCmd() {
      if (this.historyPos > 0) this.historyPos--;

      const i = this.historyPos;
      this.cmd = this.historyCmd[i];
    },

    nextCmd() {
      if (this.historyPos < this.historyCmd.length) this.historyPos++;

      const i = this.historyPos;
      this.cmd = this.historyCmd[i];
    },

    caretPos() {
      const caretPos = (this.$refs.myInput.value.length - this.$refs.myInput.selectionStart) * 9;

      this.$refs.caret.style.transform = 'translateX(-' + caretPos + 'px)';
    },

    scrollTop() {
      const scrollArea = this.$refs.developer;

      return setTimeout(function () {
        scrollArea.scrollTo(0, 0);
      }, 20);
    },

    scrollDown() {
      const scrollArea = this.$refs.developer;

      return setTimeout(function () {
        scrollArea.scrollTo(0, 1500);
      }, 30);
    },

    isPrintDone() {
      this.isPrinted = true;
    },

    mobileCheck() {
      const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
      if (isMobile) {
        this.mobile = true;
      } else {
        this.mobile = false;
      }
    }
  },

  watch: {
    '$route'(to, from) {
      this.cmd = to.params.command;
      if (this.cmd) {
        this.checkCmd();
        this.scrollTop();
      }

      if (to.name == 'commandsEmpty') {
        this.section = 'home'
        this.lastCmd = ''
      }
    },
  },

  created() {
    this.cmd = this.$route.params.command;
    if (this.cmd)
      this.checkCmd();
  },

  updated () {
    this.mobileCheck()
  },

  mounted () {
    this.mobileCheck()
    this.focusInput();
  }
}