<template>
  <RetroType :lines="lines" :charDelay="charDelay" @printdone="printDone"></RetroType>
</template>

<script>
import Lines from "../content/lines";
import RetroType from "./RetroType.vue";

export default {
  components: {
    RetroType
  },

  props: {
    section: {
      type: String,
      default: "home"
    },
    charDelay: {
      type: Number,
      default: 4
    },
  },

  data: () => ({
    lines: [],
  }),

  methods: {
    printDone() {
      this.$emit('retro-print-done');
    }
  },

  watch: {
    section: {
      immediate: true,

      handler(newSection) {
        if (Lines.hasOwnProperty(newSection)) this.lines = Lines[newSection]
      }
    }
  },
};
</script>

